/* Video section  */

.videoSection {
    width: 100%;
}

.videoSection::before {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0px;
    height: 60px;
    width: 100%;
    background: linear-gradient(to bottom, transparent , white);
    z-index: 1;
}

.videoSection img {
    flex-basis: 500px;
    flex-grow: 3;
    max-height: 415px;
}


.video {
    position: relative;
    flex-basis: 550px;
    flex-grow: 5;
}
 video {
    width: 100%;
    height: 100%;
}

.blackGradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(32, 32, 32, .7);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgb(236, 236, 236);
    text-align: center;
    background: linear-gradient(217deg, rgba(255, 255, 255, 0.8), rgba(255,0,0,0) 70.71%),
                linear-gradient(127deg, rgba(0, 119, 255, 0.8), rgba(0,255,0,0) 70.71%),
                linear-gradient(336deg, rgba(0, 0, 0, 0.8), rgba(0,0,255,0) 70.71%);
}

.blackGradient h1 {
    font-size: 50px;
    font-family: 'Fredericka the Great', cursive;
}

.blackGradient button {
    margin-top: 25px;
    padding: 10px;
    border: none;
    border-radius: 4px;
    font-size: 20px;
    font-weight: 600;
    background-color: rgb(40, 127, 241);
    color: white;
    cursor: pointer;
    transition: all .6s ease;
}

.blackGradient button:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    box-shadow: 1px 1px 1px black;
}

