/* //Imagen horizontal section  */

.seccion-imagen-horizontal {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 60px 0;
}


.seccion-imagen-horizontal img {
    width: 100%;
}

.seccion-imagen-horizontal .gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.seccion-imagen-horizontal .gradient > div {
    
    font-size: 58px;
    font-family: 'Fredericka the Great', cursive;
    text-shadow: 1px 1px black;
    color: rgb(255, 255, 255);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 15px;
    margin: 10px;
    background-color: rgba(253, 253, 253, 0.8);
    border-radius: 20px;
    color: rgb(0, 156, 255);

}

.seccion-imagen-horizontal .palabra-cambiante {
    max-height: 80px;
    overflow: hidden;

}

.seccion-imagen-horizontal .palabra-cambiante span {
    display: block;
    margin-left: 15px;
    color: rgb(0, 103, 218);
}

.seccion-imagen-horizontal .palabra-cambiante-cont {
    -webkit-animation: movimientoVerticalTexto 8s  infinite  ;
            animation: movimientoVerticalTexto 8s  infinite  ;
}

@-webkit-keyframes movimientoVerticalTexto {
    0% { -webkit-transform: translateY(0px); transform: translateY(0px); }
    20% { -webkit-transform: translateY(0px); transform: translateY(0px);}
    25% { -webkit-transform: translateY(-73px); transform: translateY(-73px); }
    45% { -webkit-transform: translateY(-73px); transform: translateY(-73px);}
    50% { -webkit-transform: translateY(-143px); transform: translateY(-143px); }
    70% { -webkit-transform: translateY(-143px); transform: translateY(-143px);}
    75% { -webkit-transform: translateY(-73px); transform: translateY(-73px); }
    95% { -webkit-transform: translateY(-73px); transform: translateY(-73px);}
    100% { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

@keyframes movimientoVerticalTexto {
    0% { -webkit-transform: translateY(0px); transform: translateY(0px); }
    20% { -webkit-transform: translateY(0px); transform: translateY(0px);}
    25% { -webkit-transform: translateY(-73px); transform: translateY(-73px); }
    45% { -webkit-transform: translateY(-73px); transform: translateY(-73px);}
    50% { -webkit-transform: translateY(-143px); transform: translateY(-143px); }
    70% { -webkit-transform: translateY(-143px); transform: translateY(-143px);}
    75% { -webkit-transform: translateY(-73px); transform: translateY(-73px); }
    95% { -webkit-transform: translateY(-73px); transform: translateY(-73px);}
    100% { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

